	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>the oil and gas sector is a key industry for Canada and an integral part of national economic growth and job creation; </p>\n</li>\n<li>\n<p>interprovincial pipeline construction presents an opportunity for nation-building and Canadian infrastructure development; </p>\n</li>\n<li>\n<p>Canada imported roughly 759,000 barrels and 736,000 barrels of oil per day, in 2016 and 2015 respectively, despite Canada having among the largest oil and gas reserves in the world, capable of meeting all Canadian oil and gas needs; </p>\n</li>\n<li>\n<p>Canadian oil and gas independence would allow Canada to ensure that domestic natural resource extraction and usage would be carried out more efficiently, safely, and sustainably than in countries which export oil and gas to Canada; </p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada urge the government to: </p>\n<ul>\n<li>\n<p>implement a strategy for oil and gas independence, with particular attention to:ostrong environmental and safety regulations, and oversight of the oil and gas sectoronational job creation and economic growth; and orespect for the rights and interests of Indigenous Peoples </p>\n</li>\n<li>\n<p>Consult with all provinces, territories, First Nations, Métis, Inuit, the Federation of Canadian Municipalities, and all other relevant stakeholders in developing acceptable infrastructure and pipelines necessary for oil and gas independence.   (Alberta)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/canadian-oil--gas-independence","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Canadian Oil & Gas Independence"}}},"pathContext":{}}

